import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { createTheme } from '@mui/material/styles';
import Ailerons from './fonts/Ailerons.woff';
import EnchantedAmulet from './/images/enchantedamulet.jpeg';
import { ThemeProvider } from "@mui/material/styles";
import React, { useState } from "react";
import { Stripe }from "@stripe/stripe-js";
import Checkout from './checkout';

const theme = createTheme({
  typography: {
    fontFamily: 'Ailerons',
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: `
        @font-face {
          font-family: 'Ailerons';
          font-style: normal;
          font-display: swap;
          font-weight: 200;
          src: local('Raleway'), local('Raleway-Regular'), url(${Ailerons}) format('woff2');
          unicodeRange: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF;
        }
      `,
    },
  },
});
const styles = {
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9,
    marginTop:'0',
  }
};

interface Props {
  title: string;
  description: string;
}
const MediaCard:React.FC<Props>=({title, description})=> {
  return (
    <Card sx={{ maxWidth: 500, minWidth:425 }}>
      <CardMedia
        sx={{ height: 200}}
        image={EnchantedAmulet} 
        title="enchanted amulet"
        style={styles.media}
      />
      <CardContent>
        <Typography gutterBottom variant="h3" component="div">
          {title}
        </Typography>
        <Typography variant="h5" color="text.secondary">
        {description} 
        </Typography>
      </CardContent>
      <CardActions>
        <Checkout>Purchase</Checkout>
        <Button size="small" >Learn More</Button>
      </CardActions>
    </Card>
  );
}


// const Item = styled(Paper)(({ theme }) => ({
//   backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
//   ...theme.typography.body2,
//   padding: theme.spacing(1),
//   textAlign: 'center',
//   color: theme.palette.text.secondary,
// }));

const FeaturedItems = () => {
  return (
    <div>
      <ThemeProvider theme={theme}>
        <div style={{margin:'0 auto 0 auto',width: '100%'}}>
      <Box alignItems="center" justifyContent="center" display="flex" margin='auto' width={{xs:"92%",sm:"80%",md:'85%',lg:'85%',xl:'85%'}}>
        <Grid container spacing={{xs:'1px',sm:'5px',md:'5px'}}>
          <Grid item xs={12} sm={12} md={12} lg={6} xl={4}>
            <MediaCard title = "Enchanted Amulet" description='This powerful Amulet is capable of bending space and time.'></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={6} xl={4}>
          <MediaCard title = "Enchanted Amulet" description='This powerful Amulet is capable of bending space and time.'></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={6} xl={4}>
          <MediaCard title = "Enchanted Amulet" description='This powerful Amulet is capable of bending space and time.'></MediaCard>
          </Grid>
          <Grid item xs={12} sm={12} md={12} lg={6} xl={4}>
          <MediaCard title = "Enchanted Amulet" description='This powerful Amulet is capable of bending space and time.'></MediaCard>
          </Grid>
        </Grid>
      </Box>
      </div>
      </ThemeProvider>
    </div>
  );
};
export default FeaturedItems;